import React from 'react'
import { Link } from 'react-router-dom'

const $ = window.$

const files = [
    '/img/absolute_vodla.jpg',
    '/img/cachaca_velho.jpg',
    '/img/sakeik_sake.jpg',
    '/img/smirnoff_vodka.jpg'
]

export default class Bebida extends React.PureComponent {
    componentDidMount() {
        $(".buddy").on("swiperight", function () {
            $(this).addClass('rotate-left').delay(700).fadeOut(1)
            $('.buddy').find('.status').remove()
            $(this).append('<div class="status like">Like!</div>')
            let coctel = JSON.parse(window.sessionStorage.getItem('coctel'))
            coctel.bebida = $(this)[0].firstChild.style.backgroundImage.split('"')[1]
            window.sessionStorage.setItem('coctel', JSON.stringify(coctel))
            setTimeout(() => document.querySelector('#btn').click(), 700)
        })
        $(".buddy").on("swipeleft", function () {
            $(this).addClass('rotate-right').delay(700).fadeOut(1)
            $('.buddy').find('.status').remove()
            $(this).append('<div class="status dislike">Dislike!</div>')
            if ($(this).is(':last-child')) {
                $('.buddy:nth-child(1)').removeClass('rotate-left rotate-right').fadeIn(300);
            } else {
                $(this).next().removeClass('rotate-left rotate-right').fadeIn(400);
            }
        })
    }
    render(){
        return (
            <div id="container">
                <h3>Escolha uma Bebida</h3>
                <div>
                    {
                        files.map((file, key)=> <div className='buddy' key={key}>
                            <div className='avatar' style={{backgroundImage: `url(${file})`}}></div>
                        </div>)
                    }
                </div>
                <Link hidden to='pronto' id="btn"/>
            </div>
        )
    }
}