import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link
}  from 'react-router-dom'

import Home from './Pages/Home'
import Criar from './Pages/Criar'
import Taca from './Pages/Taca'
import Fruta from './Pages/Fruta'
import Bebida from './Pages/Bebida'
import Pronto from './Pages/Pronto'

class App extends Component {
  render() {
    return (
      <Router>
        <div>
          <header className='header'>
            <div></div>
            <Link to='/' className='link'>
                <h4>Meu Cocktail</h4>
            </Link>
            <Link to='/criar' className='link'>Criar Cocktail</Link>
          </header>
          <div className='container'>
              <Route path='/' exact component={Home}/>
              <Route path='/criar' component={Criar}/>
              <Route path='/taca' component={Taca}/>
              <Route path='/fruta' component={Fruta}/>
              <Route path='/bebida' component={Bebida}/>
              <Route path='/pronto' component={Pronto}/>
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
