import React from 'react'
import { Link } from 'react-router-dom'

export default () => (
    <div id='container'>
        <div className='field'>
            <input type='text' name='fullname' id='fullname' placeholder=' '/>
            <label htmlFor='fullname'>Nome do Coctel</label>
        </div>
        <button onClick={()=> {
            let coctel = {}
            coctel.nome = document.querySelector('#fullname').value
            window.sessionStorage.setItem('coctel', JSON.stringify(coctel))
            document.querySelector('.button').click()
        }}>Adicionar</button>
        <Link className='button' to='/taca' hidden></Link>
    </div>
)