import React from 'react'

export default () => {
    let coctel = JSON.parse(window.sessionStorage.getItem('coctel'))
    return (
        <div id='container'>
            <div className='data-buddy'>
                <div className='data-avatar' style={{backgroundImage: `url(${coctel.bebida})`}}></div>
                <div className='data-avatar' style={{backgroundImage: `url(${coctel.fruta})`}}></div>
                <div className='data-avatar' style={{backgroundImage: `url(${coctel.taca})`}}></div>
            </div>
            <h1 style={{textTransform: 'uppercase', textAlign: 'center'}}>{coctel.nome}</h1>
        </div>
    )
}